@import url("https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500&family=Open+Sans:wght@400;600;700&family=Roboto+Slab:wght@700&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

* {
  line-height: 120%;
}

.container {
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 10px;
}

.block-name {
  font-family: "Roboto Slab", serif;
  font-weight: 700;
  font-size: 16px;
  color: #244886;
  text-align: center;
  text-transform: uppercase;
}

.title {
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
  font-size: 36px;
  color: #0F2137;
  text-align: center;
}
@media screen and (max-width: 768px) {
  .title {
    font-size: 24px;
  }
}

.sub-title {
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
  font-size: 18px;
  color: #0F2137;
}

.text {
  font-family: "DM Sans", sans-serif;
  font-weight: 400;
  font-size: 15px;
  color: #343D48;
}

.header {
  background-color: #244886;
  background-image: url(../img/header__pattern.png);
  padding: 30px 0 0 0;
  border-bottom-right-radius: 300px;
  overflow: hidden;
}
.header__header {
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 10px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  font-family: "Roboto Slab", serif;
  font-weight: 700;
  font-size: 28px;
  color: #fff;
  padding: 4px 12px;
  border: 1px solid #fff;
  z-index: 3;
}
.header__menu ul {
  display: flex;
  gap: 30px;
}
.header__menu a {
  font-family: "Open Sans", sans-serif;
  font-size: 20px;
  font-weight: 400;
  color: #fff;
  position: relative;
}
.header__menu a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background: #fff;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.5s;
}
.header__menu a:hover::after {
  transform: scaleX(1);
  transform-origin: left;
}
.header .burger-btn {
  display: none;
  z-index: 10;
  position: relative;
  width: 30px;
  height: 20px;
  cursor: pointer;
}
.header .burger-btn > span {
  position: absolute;
  background-color: #fff;
  width: 100%;
  height: 3px;
  border-radius: 18px;
  left: 0;
  top: 9px;
  transition: all ease 0.5s;
}
.header .burger-btn:after, .header .burger-btn:before {
  content: "";
  position: absolute;
  background-color: #fff;
  width: 100%;
  height: 3px;
  border-radius: 18px;
  left: 0;
  transition: all ease 0.5s;
}
.header .burger-btn:before {
  top: 20px;
}
.header .burger-btn:after {
  bottom: 20px;
}
.header .burger-btn_active span {
  opacity: 0;
}
.header .burger-btn_active:before {
  transform: rotate(45deg);
  top: 9px;
}
.header .burger-btn_active:after {
  transform: rotate(-45deg);
  bottom: 9px;
}
.header__content {
  width: 100%;
  display: flex;
  align-items: center;
  padding-left: max(10px, (100% - 1220px) / 2);
}
.header__left {
  flex-shrink: 0;
  max-width: 640px;
  width: 100%;
}
.header__title {
  font-family: "Roboto Slab", serif;
  font-weight: 700;
  font-size: 72px;
  margin-bottom: 5px;
  color: #fff;
}
.header__sub-title {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  font-size: 54px;
  color: #fff;
  margin-bottom: 20px;
}
.header__text {
  font-family: "DM Sans", sans-serif;
  font-weight: 500;
  font-size: 18px;
  color: #fff;
  max-width: 510px;
  line-height: 42px;
}
.header__right {
  width: calc(100% - 640px);
}
.header__right img {
  width: 100%;
}
@media screen and (max-width: 768px) {
  .header {
    border-radius: unset;
  }
  .header .burger-btn {
    display: block;
  }
  .header__header {
    margin-bottom: 100px;
  }
  .header__menu {
    position: absolute;
    max-width: none;
    text-align: center;
    z-index: 2;
    transform: scaleY(0);
    transform-origin: top;
    left: 0;
    top: -30%;
    width: 100%;
    padding: 95px 20px 55px 20px;
    background: #0F2137;
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: flex-end;
    flex-direction: column;
    transition: all ease 0.5s;
  }
  .header__menu_active {
    top: 0;
    transform: scaleY(1);
    box-shadow: 0 0 25px 40px rgba(0, 0, 0, 0.25);
  }
  .header__menu ul {
    flex-direction: column;
    align-items: flex-end;
  }
  .header__content {
    flex-direction: column;
    padding: 0 10px;
  }
  .header__title {
    font-size: 42px;
    margin-bottom: 30px;
  }
  .header__sub-title {
    font-size: 24px;
    margin-bottom: 20px;
  }
  .header__text {
    font-size: 18px;
    line-height: 130%;
  }
  .header__right {
    max-width: 300px;
    width: 100%;
  }
}

.about-us {
  padding: 65px 0 35px 0;
}
.about-us__top {
  max-width: 1020px;
  padding: 0 10px;
  margin: 0 auto;
}
.about-us .block-name {
  margin-bottom: 10px;
}
.about-us .title {
  margin-bottom: 100px;
}
.about-us__content {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 150px;
}
.about-us__option {
  max-width: 265px;
  text-align: center;
}
.about-us__option .sub-title {
  margin-bottom: 20px;
}
.about-us__img {
  padding: 27px 27px 28px 27px;
  border-radius: 30px;
  max-width: 110px;
  margin: 0 auto 20px auto;
}
.about-us .green {
  background: #e4ffee;
}
.about-us .blue {
  background: rgba(37, 99, 255, 0.0745098039);
}
.about-us .red {
  background: rgba(255, 67, 98, 0.1098039216);
}
.about-us .text {
  line-height: 28px;
}
.about-us__bottom {
  display: flex;
  align-items: center;
  padding-right: max(10px, (100% - 1220px) / 2);
  gap: 45px;
}
.about-us__left {
  width: calc(100% - 570px - 45px);
}
.about-us__left img {
  width: 100%;
}
.about-us__right {
  flex-shrink: 0;
  max-width: 570px;
  width: 100%;
}
.about-us__right .block-name {
  text-align: left;
  margin-bottom: 15px;
}
.about-us__right .title {
  text-align: left;
  margin-bottom: 50px;
}
.about-us__right img {
  display: none;
}
.about-us__block {
  max-width: 500px;
  width: 100%;
  background: #fff;
  padding: 25px 35px 20px 30px;
  display: flex;
  border-radius: 10px;
  margin-bottom: 20px;
  transition: all ease 0.5s;
}
.about-us__block:last-child {
  margin-bottom: 0;
}
.about-us__block:hover {
  box-shadow: 0px 8px 24px rgba(69, 88, 157, 0.07);
}
.about-us__profile {
  width: 57px;
  height: 57px;
  background: rgba(37, 99, 255, 0.0745098039);
  margin-right: 15px;
  border-radius: 25px;
}
.about-us__description {
  max-width: 355px;
  width: 100%;
}
.about-us__description .sub-title {
  margin-bottom: 20px;
}
.about-us__description .text {
  line-height: 26px;
}
@media screen and (max-width: 768px) {
  .about-us .title {
    margin-bottom: 25px;
  }
  .about-us__content {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 55px;
  }
  .about-us__option {
    margin-bottom: 40px;
  }
  .about-us__bottom {
    padding: 0 10px;
    justify-content: center;
  }
  .about-us__left {
    display: none;
  }
  .about-us__right .block-name, .about-us__right .title {
    text-align: center;
  }
  .about-us__right img {
    display: block;
    width: 100%;
  }
}
@media screen and (max-width: 350px) {
  .about-us__block {
    flex-direction: column;
    gap: 10px;
    padding: 15px 5px 10px 10px;
  }
}

.consultation {
  background-color: #244886;
  background-image: url(../img/consultation__pattern.png);
  padding: 90px 0 80px 0;
}
.consultation__wrapper {
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 10px;
}
.consultation .block-name {
  margin-bottom: 15px;
  color: #fff;
  opacity: 0.7;
}
.consultation .title {
  margin-bottom: 100px;
  color: #fff;
}
.consultation__arrows {
  display: none;
  max-width: 90px;
  width: 100%;
  margin-left: auto;
  margin-bottom: 50px;
}
.consultation__prev {
  margin-right: 20px;
}
.consultation__prev, .consultation__next {
  transition: all ease 0.5s;
}
.consultation .swiper-button-disabled {
  opacity: 0.5;
}
.consultation__content {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.consultation__option {
  max-width: 260px;
  width: 100%;
}
.consultation__step {
  max-width: 72px;
  padding: 19px 19px 17px 19px;
  background: #fff;
  font-family: "Open Sans", sans-serif;
  font-size: 30px;
  color: #244886;
  text-align: center;
  margin-bottom: 40px;
  border-radius: 30px;
}
.consultation .sub-title {
  margin-bottom: 15px;
  color: #fff;
}
.consultation .text {
  color: #fff;
  opacity: 0.65;
  line-height: 26px;
}
@media screen and (max-width: 768px) {
  .consultation {
    padding: 30px 0 20px 0;
  }
  .consultation .title {
    margin-bottom: 40px;
  }
  .consultation__arrows {
    display: block;
  }
  .consultation__content {
    flex-wrap: wrap;
  }
}

.schedule {
  padding: 40px 0;
  position: relative;
}
.schedule__bg {
  position: absolute;
  width: 100%;
  height: 150px;
  background: #244886;
  top: 50%;
  z-index: -1;
}
.schedule .block-name {
  margin-bottom: 15px;
}
.schedule .title {
  margin-bottom: 60px;
}
.schedule__table {
  max-width: 380px;
  margin: 0 auto;
  padding: 35px 25px 35px 15px;
  border: 4px solid #244886;
  background: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 10px;
}
.schedule__day {
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 10px;
  line-height: 40px;
}
.schedule__line {
  height: 350px;
  width: 4px;
  background: #244886;
}
.schedule__time {
  font-family: "Roboto Slab", serif;
  font-weight: 700;
  font-size: 54px;
  text-align: center;
  max-width: 125px;
  line-height: 40px;
}
@media screen and (max-width: 768px) {
  .schedule .title {
    margin-bottom: 20px;
  }
}
@media screen and (max-width: 400px) {
  .schedule__day {
    font-size: 16px;
  }
  .schedule__time {
    max-width: 110px;
    font-size: 46px;
  }
}
@media screen and (max-width: 400px) {
  .schedule__time {
    max-width: 90px;
  }
}

.q-a {
  padding: 40px 0 20px 0;
}
.q-a__wrapper {
  max-width: 750px;
  width: 100%;
  margin: 0 auto;
}
.q-a .block-name {
  margin-bottom: 15px;
}
.q-a .title {
  margin-bottom: 40px;
}
.q-a__content {
  width: 100%;
}
.q-a__answer {
  width: 100%;
  padding: 5px 0 5px 15px;
  border-bottom: 1px solid #E5ECF4;
  margin-bottom: 15px;
}
.q-a__answer:last-child {
  margin-bottom: 0;
}
.q-a__cont {
  max-height: 50px;
  transition: all ease 0.5s;
}
.q-a__cont_active {
  max-height: 1000px;
}
.q-a__top {
  display: flex;
  gap: 15px;
  margin-bottom: 20px;
  cursor: pointer;
}
.q-a__plus {
  position: relative;
  width: 24px;
  height: 24px;
  background: #244886;
  border-radius: 50px;
}
.q-a__plus::before, .q-a__plus::after {
  content: "";
  position: absolute;
  left: 6px;
  bottom: 11px;
  width: 12px;
  height: 2px;
  background: #fff;
  border-radius: 40px;
  transition: all ease 0.5s;
}
.q-a__plus::after {
  transform: rotate(-90deg);
}
.q-a__plus_active::after {
  transform: rotate(0);
}
.q-a__name {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  font-size: 18px;
  color: #0F2137;
}
.q-a__bottom {
  padding-left: 40px;
  opacity: 0;
  transform-origin: top;
  transform: scaleY(0);
  transition: all ease 0.5s;
}
.q-a__bottom_active {
  transform: scaleY(1);
  opacity: 1;
}
.q-a .text {
  line-height: 32px;
  margin-bottom: 20px;
}

.map {
  padding: 20px 10px 120px 10px;
}
.map__wrapper {
  max-width: 1000px;
  width: 100%;
  margin: 0 auto;
}
.map .block-name {
  margin-bottom: 15px;
}
.map .title {
  margin-bottom: 70px;
}
.map iframe {
  width: 100%;
}

.footer {
  overflow: hidden;
  padding: 0 0 60px 0;
  background: #0F2137;
}
.footer__wrapper {
  max-width: 1300px;
  padding: 0 50px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer__left {
  max-width: 360px;
}
.footer__logo {
  max-width: 215px;
  font-family: "Roboto Slab", serif;
  font-weight: 700;
  font-size: 72px;
  color: #0F2137;
  padding: 25px 10px 5px 10px;
  background: #fff;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  margin-bottom: 37px;
  position: relative;
}
.footer__logo::before, .footer__logo::after {
  content: "";
  position: absolute;
  width: 56px;
  height: 45px;
}
.footer__logo::before {
  background: url(../img/footer__left.png) top right no-repeat;
  top: 0;
  left: -56px;
}
.footer__logo::after {
  right: -56px;
  top: 0;
  background: url(../img/footer__right.png) left top no-repeat;
}
.footer__text {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 23px;
  line-height: 24px;
  color: #fff;
  margin-bottom: 35px;
}
.footer__text:last-child {
  margin-bottom: 0;
}
.footer .number {
  margin-bottom: 25px;
}
.footer__right {
  max-width: 200px;
  text-align: left;
}
.footer__right .title {
  color: #fff;
  margin-bottom: 40px;
}
.footer__right ul {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.footer__right a {
  position: relative;
  font-family: "Open Sans", sans-serif;
  font-size: 23px;
  font-weight: 400;
  color: #fff;
}
.footer__right a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background: #fff;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.5s;
}
.footer__right a:hover::after {
  transform: scaleX(1);
  transform-origin: left;
}
@media screen and (max-width: 768px) {
  .footer__wrapper {
    flex-direction: column;
  }
  .footer__left {
    margin-bottom: 35px;
  }
  .footer__right {
    max-width: 360px;
    width: 100%;
  }
  .footer__right .title {
    text-align: left;
  }
}