@import "./nullstyle";
@import "./variable";
@import "./fonts";
@import "./mixins";

* {
  line-height: 120%;
}

.container {
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 10px;
}

.block-name {
  font-family: $font-family-Roboto;
  font-weight: 700;
  font-size: 16px;
  color: $primary-color;
  text-align: center;
  text-transform: uppercase;
}

.title {
  font-family: $font-family-Open;
  font-weight: 700;
  font-size: 36px;
  color: $primary-color-black;
  text-align: center;
  @media screen and (max-width:768px){
    font-size: 24px;
  }
}

.sub-title {
  font-family: $font-family-Open;
  font-weight: 700;
  font-size: 18px;
  color: $primary-color-black;
}

.text {
  font-family: $font-family-DM;
  font-weight: 400;
  font-size: 15px;
  color: $primary-color-light;
}

.header {
  background-color: $primary-color;
  background-image: url(../img/header__pattern.png);
  padding: 30px 0 0 0;
  border-bottom-right-radius: 300px;
  overflow: hidden;
  &__header {
    max-width: 1220px;
    margin: 0 auto;
    padding: 0 10px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    font-family: $font-family-Roboto;
    font-weight: 700;
    font-size: 28px;
    color: $contrast-color;
    padding: 4px 12px;
    border: 1px solid $contrast-color;
    z-index: 3;
  }
  &__menu {
    ul {
      display: flex;
      gap: 30px;
    }
    a {
      font-family: $font-family-Open;
      font-size: 20px;
      font-weight: 400;
      color: $contrast-color;
      position: relative;
      &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 2px;
        background: $contrast-color;
        transform: scaleX(0);
        transform-origin: right;
        transition: transform 0.5s;
      }
      &:hover {
        &::after {
          transform: scaleX(1);
          transform-origin: left;
        }
      }
    }
  }
  .burger-btn {
    display: none;
    z-index: 10;
    position: relative;
    width: 30px;
    height: 20px;
    cursor: pointer;
    > span {
      position: absolute;
      background-color: $contrast-color;
      width: 100%;
      height: 3px;
      border-radius: 18px;
      left: 0;
      top: 9px;
      @include transition;
    }
    &:after,
    &:before {
      content: "";
      position: absolute;
      background-color: $contrast-color;
      width: 100%;
      height: 3px;
      border-radius: 18px;
      left: 0;
      @include transition;
    }
    &:before {
      top: 20px;
    }
    &:after {
      bottom: 20px;
    }
    &_active {
      span {
        opacity: 0;
      }
      &:before {
        transform: rotate(45deg);
        top: 9px;
      }
      &:after {
        transform: rotate(-45deg);
        bottom: 9px;
      }
    }
  }
  &__content {
    width: 100%;
    display: flex;
    align-items: center;
    padding-left: max(10px, calc((100% - 1220px) / 2));
  }
  &__left {
    flex-shrink: 0;
    max-width: 640px;
    width: 100%;
  }
  &__title {
    font-family: $font-family-Roboto;
    font-weight: 700;
    font-size: 72px;
    margin-bottom: 5px;
    color: $contrast-color;
  }
  &__sub-title {
    font-family: $font-family-Open;
    font-weight: 600;
    font-size: 54px;
    color: $contrast-color;
    margin-bottom: 20px;
  }
  &__text {
    font-family: $font-family-DM;
    font-weight: 500;
    font-size: 18px;
    color: $contrast-color;
    max-width: 510px;
    line-height: 42px;
  }
  &__right {
    width: calc(100% - 640px);
    img {
      width: 100%;
    }
  }
  @media screen and (max-width:768px) {
    border-radius: unset;
    .burger-btn{
      display: block; 
    }
    &__header{
      margin-bottom: 100px;
    }
    &__menu {
      position: absolute;
      max-width: none;
      text-align: center;
      z-index: 2;
      // top: -225%;
      transform: scaleY(0);
      transform-origin: top;
      left: 0;
      top: -30%;
      width: 100%;
      padding: 95px 20px 55px 20px;
      background: $footer-color;
      @include flex($flex-direction: column, $align-items: flex-end);
      @include transition;
      &_active {
        top: 0;
        transform: scaleY(1);
        box-shadow: 0 0 25px 40px rgb(0 0 0 / 25%);
      }
      ul{
        flex-direction: column;
        align-items: flex-end;
      }
    }
    &__content{
      flex-direction: column;
      padding: 0 10px;
    }
    &__title{
      font-size: 42px;
      margin-bottom: 30px;
    }
    &__sub-title{
      font-size: 24px;
      margin-bottom: 20px;
    }
    &__text{
      font-size: 18px;
      line-height: 130%;
    }
    &__right{
      max-width: 300px;
      width: 100%;
    }
  }
}

// ABOUT-US

.about-us {
  padding: 65px 0 35px 0;
  &__top {
    max-width: 1020px;
    padding: 0 10px;
    margin: 0 auto;
  }
  .block-name {
    margin-bottom: 10px;
  }
  .title {
    margin-bottom: 100px;
  }
  &__content {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 150px;
  }
  &__option {
    max-width: 265px;
    text-align: center;
    .sub-title {
      margin-bottom: 20px;
    }
  }
  &__img {
    padding: 27px 27px 28px 27px;
    border-radius: 30px;
    max-width: 110px;
    margin: 0 auto 20px auto;
  }
  .green {
    background: #e4ffee;
  }
  .blue {
    background: #2563ff13;
  }
  .red {
    background: #ff43621c;
  }
  .text {
    line-height: 28px;
  }

  // about-us__bottom

  &__bottom {
    display: flex;
    align-items: center;
    padding-right: max(10px, calc((100% - 1220px) / 2));
    gap: 45px;
  }
  &__left {
    width: calc(100% - 570px - 45px);
    img {
      width: 100%;
    }
  }
  &__right {
    flex-shrink: 0;
    max-width: 570px;
    width: 100%;
    .block-name {
      text-align: left;
      margin-bottom: 15px;
    }
    .title {
      text-align: left;
      margin-bottom: 50px;
    }
    img{
      display: none;
    }
  }
  &__block {
    max-width: 500px;
    width: 100%;
    background: $contrast-color;
    padding: 25px 35px 20px 30px;
    display: flex;
    border-radius: 10px;
    margin-bottom: 20px;
    @include transition;
    &:last-child {
      margin-bottom: 0;
    }
    &:hover {
      box-shadow: 0px 8px 24px rgba(69, 88, 157, 0.07);
    }
  }
  &__profile {
    width: 57px;
    height: 57px;
    background: #2563ff13;
    margin-right: 15px;
    border-radius: 25px;
  }
  &__description {
    max-width: 355px;
    width: 100%;
    .sub-title {
      margin-bottom: 20px;
    }
    .text {
      line-height: 26px;
    }
  }
  @media screen and (max-width:768px){
    .title{
      margin-bottom: 25px;
    }
    &__content{
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin-bottom: 55px;
    }
    &__option{
      margin-bottom: 40px;
    }
    &__bottom{
      padding: 0 10px;
      justify-content: center;
    }
    &__left{
      display: none;
    }
    &__right{
      .block-name, .title{
        text-align: center;
      }
      img{
        display: block;
        width: 100%;
      }
    }
  }
  @media screen and (max-width:350px) {
    &__block{
      flex-direction: column;
      gap: 10px;
      padding: 15px 5px 10px 10px;
    }
  }
}

// CONSULTATION

.consultation {
  background-color: $primary-color;
  background-image: url(../img/consultation__pattern.png);
  padding: 90px 0 80px 0;
  &__wrapper {
    max-width: 1220px;
    margin: 0 auto;
    padding: 0 10px;
  }
  .block-name {
    margin-bottom: 15px;
    color: $contrast-color;
    opacity: 0.7;
  }
  .title {
    margin-bottom: 100px;
    color: $contrast-color;
  }
  &__arrows{
    display: none;
    max-width: 90px;
    width: 100%;
    margin-left: auto;
    margin-bottom: 50px;
  }
  &__prev{
    margin-right: 20px;
  }
  &__prev, &__next{
    @include transition;
  }
  .swiper-button-disabled{
    opacity: 0.5;
  }
  &__content {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  &__option {
    max-width: 260px;
    width: 100%;
  }
  &__step {
    max-width: 72px;
    padding: 19px 19px 17px 19px;
    background: $contrast-color;
    font-family: $font-family-Open;
    font-size: 30px;
    color: $primary-color;
    text-align: center;
    margin-bottom: 40px;
    border-radius: 30px;
  }
  .sub-title {
    margin-bottom: 15px;
    color: $contrast-color;
  }
  .text {
    color: $contrast-color;
    opacity: 0.65;
    line-height: 26px;
  }
  @media screen and (max-width:768px) {
    padding: 30px 0 20px 0;
    .title{
      margin-bottom: 40px;
    }
    &__arrows{
      display: block; 
    }
    &__content{
      flex-wrap: wrap;
    }
  }
}

// SCHEDULE

.schedule {
  padding: 40px 0;
  position: relative;
  &__bg {
    position: absolute;
    width: 100%;
    height: 150px;
    background: $primary-color;
    top: 50%;
    z-index: -1;
  }
  .block-name {
    margin-bottom: 15px;
  }
  .title {
    margin-bottom: 60px;
  }
  &__table {
    max-width: 380px;
    margin: 0 auto;
    padding: 35px 25px 35px 15px;
    border: 4px solid $primary-color;
    background: $contrast-color;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
  }
  &__day {
    font-family: $font-family-Open;
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 10px;
    line-height: 40px;
  }
  &__line {
    height: 350px;
    width: 4px;
    background: $primary-color;
  }
  &__time {
    font-family: $font-family-Roboto;
    font-weight: 700;
    font-size: 54px;
    text-align: center;
    max-width: 125px;
    line-height: 40px;
  }
  @media screen and (max-width:768px){
    .title{
      margin-bottom: 20px;
    }
  }
  @media screen and (max-width:400px){
    &__day{
      font-size: 16px;
    }
    &__time{
      max-width: 110px;
      font-size: 46px;
    }
  }
  @media screen and (max-width:400px){
    &__time{
      max-width: 90px;
    }
  }
}

// Q&A

.q-a {
  padding: 40px 0 20px 0;
  &__wrapper{
    max-width: 750px;
    width: 100%;
    margin: 0 auto;
  }
  .block-name{
    margin-bottom: 15px;
  }
  .title{
    margin-bottom: 40px;
  }
  &__content{
    width: 100%;
  }
  &__answer{
    width: 100%;
    padding: 5px 0 5px 15px;
    border-bottom: 1px solid #E5ECF4;
    margin-bottom: 15px;
    &:last-child{
      margin-bottom: 0;
    }
  }
  &__cont{
    max-height: 50px;
    @include transition;
    &_active{
      max-height: 1000px;
    }
  }
  &__top{
    display: flex;
    gap: 15px;
    margin-bottom: 20px;
    cursor: pointer;
  }
  &__plus{
    position: relative;
    width: 24px;
    height: 24px;
    background: $primary-color;
    border-radius: 50px;
    &::before, &::after{
      content: '';
      position: absolute;
      left: 6px;
      bottom: 11px;
      width: 12px;
      height: 2px;
      background: $contrast-color;
      border-radius: 40px;
      @include transition;
    }
    &::after{
      transform: rotate(-90deg);
    }
    &_active{
      &::after{
        transform: rotate(0);
      }
    }
  }
  &__name{
    font-family: $font-family-Open;
    font-weight: 600;
    font-size: 18px;
    color: $primary-color-black;
  }
  &__bottom{
    padding-left: 40px;
    opacity: 0;
    transform-origin: top;
    transform: scaleY(0);
    @include transition;
    &_active{
      transform: scaleY(1);
      opacity: 1;
    }
  }
  .text{
    line-height: 32px;
    margin-bottom: 20px;
  }
}

// MAPS

.map{
  padding: 20px 10px 120px 10px;
  &__wrapper{
    max-width: 1000px;
    width: 100%;
    margin: 0 auto;
  }
  .block-name{
    margin-bottom: 15px;
  }
  .title{
    margin-bottom: 70px;
  }
  iframe{
    width: 100%;
  }
}

// FOOTER

.footer{
  overflow: hidden;
  padding: 0 0 60px 0;
  background: $footer-color;
  &__wrapper{
    max-width: 1300px;
    padding: 0 50px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left{
    max-width: 360px;
  }
  &__logo{
    max-width: 215px;
    font-family: $font-family-Roboto;
    font-weight: 700;
    font-size: 72px;
    color: $footer-color;
    padding: 25px 10px 5px 10px;
    background: $contrast-color;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    margin-bottom: 37px;
    position: relative;
    &::before, &::after{
      content: '';
      position: absolute;
      width: 56px;
      height: 45px;
    }
    &::before{
      background: url(../img/footer__left.png) top right no-repeat;
      top: 0;
      left: -56px;
    }
    &::after{
      right: -56px;
      top: 0;
      background: url(../img/footer__right.png) left top no-repeat;
    }
  }
  &__text{
    font-family: $font-family-Open;
    font-weight: 400;
    font-size: 23px;
    line-height: 24px;
    color: $contrast-color;
    margin-bottom: 35px;
    &:last-child{
      margin-bottom: 0;
    }
  }
  .number{
    margin-bottom: 25px;
  }
  &__right{
    max-width: 200px;
    text-align: left;
    .title{
      color: $contrast-color;
      margin-bottom: 40px;
    }
    ul{
      display: flex;
      flex-direction: column;
      gap: 15px;
    }
    a{
      position: relative;
      font-family: $font-family-Open;
      font-size: 23px;
      font-weight: 400;
      color: $contrast-color;
      &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 2px;
        background: $contrast-color;
        transform: scaleX(0);
        transform-origin: right;
        transition: transform 0.5s;
      }
      &:hover {
        &::after {
          transform: scaleX(1);
          transform-origin: left;
        }
      }
    }
  }
  @media screen and (max-width:768px) {
    &__wrapper{
      flex-direction: column;
    }
    &__left{
      margin-bottom: 35px;
    }
    &__right{
      max-width: 360px;
      width: 100%;
      .title{
        text-align: left;
      }
    }
  }
}